.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

body {
  background-color: rgb(190, 255, 220);
  padding-bottom: 40px;
  }

h1 {
  text-align: center;
  font-family: Arial, monospace, sans-serif;
  font-size: 400%;
}

h2 {
  text-align: center;
  font-family: Arial, monospace, sans-serif;
  font-size: 200%;
}

p { 
  display: inline-block;
  max-width: 40%;
}

.center {
  text-align: center;
  justify-content: center;
}

img {
  border-radius: 8px;
  max-width: 25%;
}

.flex-container {
  display: flex;
  align-items: center;
  justify-content: center;
}

.flex-container > div {
  margin: 10px;
  padding: 20px;
}